import React from 'react';
import { Card, CardContent, Typography, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, CircularProgress } from '@mui/material';

function RequestCard({ request, onUpdateStatus, onUpdateItemStatus, updatingId }) {
  const isAllCompleted = request.items.every(item => item.status.trim() === '');

  const handleAllComplete = () => {
    onUpdateStatus(request.id, '');
  };

  const handleItemComplete = (item) => {
    onUpdateItemStatus({
      orderId: request.id,
      itemName: item.subcategory,
      thickness: item.thickness,
      size: item.size,
      newStatus: ''
    });
  };

  return (
    <Card sx={{ mb: 2 }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
          <div>
            <Typography variant="h6">: {request.id}</Typography>
            <Typography color="textSecondary">: {request.user} ({request.userPhone})</Typography>
            <Typography color="textSecondary">: {request.project} - {request.term} - {request.engineeringItem}</Typography>
            <Typography color="textSecondary">: {request.deliveryAddress}</Typography>
            <Typography color="textSecondary">: {request.deliveryDate}</Typography>
            <Typography color="textSecondary">: {request.recipientName} ({request.recipientPhone})</Typography>
          </div>
          {!isAllCompleted && (
            <Button 
              variant="contained" 
              color="primary" 
              onClick={handleAllComplete}
              disabled={updatingId === request.id}
              sx={{ minWidth: '100px' }}
            >
              {updatingId === request.id ? <CircularProgress size={24} color="inherit" /> : ''}
            </Button>
          )}
        </Box>
        <TableContainer component={Paper} sx={{ mt: 2 }}>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell align="right"></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {request.items.map((item, index) => {
                const uniqueItemId = `${request.id}-${item.subcategory}-${item.thickness}-${item.size}`;
                const isUpdating = updatingId === uniqueItemId;
                return (
                  <TableRow key={index}>
                    <TableCell>{item.category} - {item.subcategory}</TableCell>
                    <TableCell>{item.thickness}</TableCell>
                    <TableCell>{item.size}</TableCell>
                    <TableCell align="right">{item.quantity}</TableCell>
                    <TableCell>{item.unit}</TableCell>
                    <TableCell>{item.status}</TableCell>
                    <TableCell>
                      {item.status.trim() !== '' && (
                        <Button 
                          variant="outlined" 
                          size="small" 
                          onClick={() => handleItemComplete(item)}
                          disabled={isUpdating}
                          sx={{ minWidth: '60px' }}
                        >
                          {isUpdating ? <CircularProgress size={20} color="inherit" /> : ''}
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </CardContent>
    </Card>
  );
}

export default RequestCard;
