import React from 'react';
import { Card, CardContent, Typography, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, CircularProgress } from '@mui/material';

function ReturnCard({ ret, onUpdateStatus, onUpdateItemStatus, updatingId }) {
  const isAllCompleted = ret.items.every(item => item.status.trim() === '');

  const handleAllComplete = () => {
    onUpdateStatus(ret.id, '');
  };

  const handleItemComplete = (item) => {
    onUpdateItemStatus({
      returnId: ret.id,
      itemName: item.name,
      newStatus: ''
    });
  };

  return (
    <Card sx={{ mb: 2 }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
          <div>
            <Typography variant="h6">: {ret.id}</Typography>
            <Typography color="textSecondary">: {ret.user}</Typography>
            <Typography color="textSecondary">: {ret.project}</Typography>
          </div>
          {!isAllCompleted && (
            <Button 
              variant="contained" 
              color="primary" 
              onClick={handleAllComplete}
              disabled={updatingId === `return-${ret.id}`}
              sx={{ minWidth: '100px' }}
            >
              {updatingId === `return-${ret.id}` ? <CircularProgress size={24} color="inherit" /> : ''}
            </Button>
          )}
        </Box>
        <TableContainer component={Paper} sx={{ mt: 2 }}>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell align="right"></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {ret.items.map((item, index) => {
                const uniqueItemId = `return-${ret.id}-${item.name}`;
                const isUpdating = updatingId === uniqueItemId;
                return (
                  <TableRow key={index}>
                    <TableCell>{item.name}</TableCell>
                    <TableCell align="right">{item.quantity}</TableCell>
                    <TableCell>{item.reason}</TableCell>
                    <TableCell>{item.status}</TableCell>
                    <TableCell>
                      {item.status.trim() !== '' && (
                        <Button 
                          variant="outlined" 
                          size="small" 
                          onClick={() => handleItemComplete(item)}
                          disabled={isUpdating}
                          sx={{ minWidth: '60px' }}
                        >
                          {isUpdating ? <CircularProgress size={20} color="inherit" /> : ''}
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </CardContent>
    </Card>
  );
}

export default ReturnCard;